*{margin:0;padding:0;}

.float{width:100%;height: 4rem;}
.float .ipt{float: left;width:1rem;height:.9rem;margin:.8rem;}
.float .imags{width:3.7rem;height:3.5rem;float: left;margin-left: .5rem;}
.float .imags .imag{width:100%;height: 100%;border-radius: .2rem;}
.float .box{float: left;margin-left: .5rem;height:3rem;margin-right: .5rem;}
.float .box .box-shop{padding: 0;margin: 0;font-size: .6rem;}
.float .box .box-shop a{font: .5rem '';color: #586c94;}
.float .box .pt3{color: #586c94;font: .5rem '';}
.float .box .price{color: #586c94;}
.float .box .price em{font: .5rem '';}

.float .btn-group{float:left;width:6rem;height:4rem;margin:.2rem auto;}
.float .btn-group .btn{float: left;width:1.8rem;margin-left: .2rem;margin-top: 1.2rem;background: #333;color: #fff;}
.float .delete{float: right;width:3rem;height:2rem;line-height: 4rem;}
.float .delete input{width:3rem;height:2rem;text-align: center;background: gray;margin-right: .6rem;}

.goIndex button{position: fixed;right:8.4rem;bottom:3rem;background: grey;width:4rem;height:2.5rem;
border-radius: .4rem;}
.clearCart button{position: fixed;right:1.4rem;bottom:3rem;background: grey;width:5rem;height:2.5rem;
    border-radius: .4rem;}

.footer{height:2.5rem;}
.footer-box{background: #454545;width:100%;height:2.5rem;position: fixed;left:0;bottom:0;}
.footer-box .box{border-radius: 50%;width:1rem;height:.9rem;margin:.8rem;float: left;}
.footer-box .box .ipt{width:100%;height:100%;}
.footer-box span{float: left;font: .8rem "";line-height: 1.2rem;}
.footer-box .npd{float: left;margin-left: .4rem;}
.footer-box .npd .pt{font: .9rem '';line-height: 2.4rem;}
.footer-box a .pt2{margin-left: 4.4rem;font: 1rem '';color: #fff;line-height: 2.5rem;display: inline-block;}