.img{background: coral;overflow: hidden;}
.img img{
    height: 80px;
    width: 80px;
    border-radius: 50%;
    margin: 30px auto;
    vertical-align: middle;
    float: left;
}
.img span{
    float: left;
    vertical-align: middle;
    font: bold 24px "";
    color: #fff;
    margin-left: 10px;
}
span.span{
    font: 14px '';
    float: left;
    margin: 1.8rem;
}
.ul{
    width:100%;
    height:2.6rem;
    margin: 0 auto;
    background:orangered;

}
.ul ul{overflow: hidden;text-align: center;}
.ul ul li{float: left;margin-left:6.3rem;}

.transaction{
    overflow: hidden;
    margin: 10px;
    border: 1px solid #cdcdcd;
}
.transaction img{
    width: 20px;
    height: 20px;
    margin: 10px;
    float: left;
}
.transaction p{
    float: left;
    margin-top: 15px;
}

.arrow{
    float: right;
    color: #cdcdcd;
    padding-top: 12px;
    padding-right: 5px;
    vertical-align: middle;
}
.all{
    margin-top:40px;
}

