*{margin: 0;padding:0;}
.foot-black{height:48px; clear:both;}
.weui-tabbar.wy-foot-menu{background:#fff;position:fixed;left:0;bottom:0;display: flex;justify-content: space-around;width: 100%;}
.weui-tabbar__item.weui-bar__item--on .weui-tabbar__label{color:#e21323;}
.weui-tabbar__label{color:#666;}
.weui-tabbar__icon{width:22px; height:22px;}
.weui-tabbar__item .foot-menu-home{background:url(../img/footer01.png) no-repeat; background-size:22px;}
.weui-tabbar__item.weui-bar__item--on .foot-menu-home{background:url(../img/footer001.png) no-repeat; background-size:22px;}
.weui-tabbar__item .foot-menu-list{background:url(../img/footer02.png) no-repeat; background-size:22px;}
.weui-tabbar__item.weui-bar__item--on .foot-menu-list{background:url(../img/footer002.png) no-repeat; background-size:22px;}
.weui-tabbar__item .foot-menu-cart{background:url(../img/footer03.png) no-repeat; background-size:22px;}
.weui-tabbar__item.weui-bar__item--on .foot-menu-cart{background:url(../img/footer003.png) no-repeat; background-size:22px;}
.weui-tabbar__item .foot-menu-member{background:url(../img/footer04.png) no-repeat; background-size:22px;}
.weui-tabbar__item.weui-bar__item--on .foot-menu-member{background:url(../img/footer004.png) no-repeat; background-size:22px;}