html,body{ overflow-x: hidden; }
.detail .nav{width:100%; position:fixed;top:0;left:0; background:cornflowerblue; border-bottom:1px solid #e8eaec; z-index:99;}
.detail .nav div.ul{width:6rem;height:1.8rem; padding-top:0.15rem; margin:.2rem;}
.detail .nav div.ul b{display:block;width:1rem;height:1rem; background:url(../img/history.png) no-repeat 0 0; background-size:100%; margin:.1rem 0 0 0.3rem;}

.detail .content{margin:0 auto; margin-top:2.4rem; background:#f2f4f5; padding-bottom:0.85rem;}
.detail .content .header{ padding:0.39rem 0.28rem 0.15rem 0.28rem; height:auto; background: none}
.detail .header h2{ float:left; margin-right:1.3rem;}
.detail .header p{ float:left; margin-top:0.3rem; font-size:1rem;}

.detail .content .cont{ padding:0 0.38rem; color:#494d4d;}
.detail .cont h3{ font-size:1rem; line-height:2rem; margin-top: 3.3rem; padding-bottom:1.2rem; border-bottom:1px solid #494d4d;}
.detail .cont .time{height:0.24rem; line-height:0.5rem; margin:0.5rem .2rem; }
.detail .time p{ float:left;position:relative;line-height:1.2rem;}
.detail .time span{width:1rem;height:1rem; position:absolute; top:-0.02rem;right:-1.5rem;}
.detail .time span img{width:100%;height:100%;}

.detail .cont .text-box{ font:1rem/1.4rem "";width: 90%;margin: 2.2rem auto;text-indent:1.6rem;}
.detail .text-box p{ line-height:1.45rem; margin-bottom:0.1rem;}
.detail .foot-btn{width:100%;height:1.8rem;background:cornflowerblue;justify-content: space-around; border-top:1px solid #e8eaec; position:fixed; left:0;bottom:0;}
.detail .foot-btn ul{width:100%;height:1.4rem; margin-top:0.16rem;}
.detail .foot-btn ul li{float: left; list-style: none;margin: .2rem;margin-left: .4rem;margin-right: .5rem;padding-right:1rem;}
.detail .foot-btn ul li a{width:100%;height:100%; display:block;margin-left: .4rem;margin-right: .4rem;}
.detail .foot-btn ul .say{width:3.8rem;height:1.5rem; border-right:1px solid #e8eaec;}
.detail .say i{width:1.36rem;height:1.28rem; background:url(../img/say.png) no-repeat 0 0; background-size:100%; float:left; margin-left:0.7rem; margin-top:0.13rem;}
.detail .say span{height:0.5rem; float:left; line-height:0.26rem; margin-left:0.3rem; margin-top:0.5rem;}
.detail .foot-btn ul .zan{width:3.86rem;height:1.5rem; border-right:1px solid #e8eaec;}
.detail .zan i{width:1.36rem;height:1.28rem; background:url(../img/zan1.png) no-repeat 0 0; background-size:100%; float:left; margin-left:0.54rem; margin-top:0.13rem;}
.detail .zan span{height:0.5rem; float:left; line-height:0.26rem; margin-left:0.3rem; margin-top:0.5rem;}
.detail .foot-btn ul .xing{width:3.2rem;height:1.5rem; border-right:1px solid #e8eaec;}
.detail .xing i{width:1.34rem;height:1.24rem; margin:0 auto; display:block; padding-top:0.1rem;}
.detail .xing i img{width:100%;}
.detail .foot-btn ul .fx{width:3.25rem;height:1.5rem;}
.detail .fx i{width:1.33rem;height:1.08rem;display:block; margin:0 auto; padding-top:0.15rem;}
.detail .fx i img{width:100%;}
.btn{width:50%;;margin: 0 auto; color: #fff;}
.btn .button{width:80%;height:2.4rem;background: mediumblue;margin: .6rem;}