.form--heading{
    font:bold 24px/2 "";
    margin: 60px auto;
    width: 70%;
    color: dodgerblue;
    text-align: center;
}

input.name{
    border-radius:4px;
    border:1px solid #cdcdcd;
    margin: 20px auto;
    display: block;
    height: 40px;
    width: 60%;
    padding: 5px;
}
.reg .fhbtn{
    padding-top:0.27rem;
}
.reg .fhbtn a{
    display:block;
    width:1.23rem;
    height:1.25rem;
    background:url(../img/history.png) no-repeat 0 0;
    background-size:100%;
    margin-left:0.3rem;
}
.reg{
    background: #15C8DA;
    height:640px;
}
.icon{
    background: #f9f;
    color: #fff;
}
.file input#icon{
    background: skyblue;
    width: 12.4rem;
    text-align: center;
    display:block;
    margin: .3rem auto;
    height:1.6rem;
    color: #ff99ff;
}
.button{
    margin: 50px auto;
    width: 62%;
    background: dodgerblue;
    height: 50px;
    text-align: center;
    border: 1px solid #333;
    display: block;
    border-radius: 3px;
}
